import React, { useState } from "react";
import {
  Button,
  Form,
  Input,
  Radio,
  Table,
  Popconfirm,
  message,
  Icon
} from "antd";
import { axios, apis, qs } from "../../api";
import ContributeForm from "./ContributeForm";

const Contribute = ({
  form,
  application,
  contributeCallback,
  cookies = [],
  onApplicationChange,
  deleteCookieCallback
}) => {
  const [loading, setLoading] = useState(false);
  const [contributeMode, setContributeMode] = useState(
    localStorage.getItem("contributeMode") || "sid-and-snsInfo"
  );
  const { getFieldDecorator } = form;

  const handleSubmit = event => {
    event.preventDefault();
    form.validateFields(async (err, values) => {
      if (!err) {
        setLoading(true);
        try {
          const params = {
            value: values.cookie,
            application
          };
          if (application === 1 && contributeMode === "sid-and-snsInfo") {
            params.value = `SID=${values.sid}; snsInfo[101204453]=${
              values.snsInfo
            };`;
          }
          const data = await axios.post(apis.cookie, qs.stringify(params));
          if (data.code === 0) {
            message.success("");
            contributeCallback();
          } else {
            message.error(data.message);
          }
        } catch (e) {
          message.error(e.message);
        } finally {
          setLoading(false);
          form.resetFields();
        }
      }
    });
  };

  const changeContributeMode = event => {
    const { value } = event.target;
    setContributeMode(value);
    localStorage.setItem("contributeMode", value);
  };

  const deleteCookies = async () => {
    const data = await axios.delete(apis.deleteCookie);
    if (data.code === 0) {
      window.location.reload();
    } else {
      message.error(data.message);
    }
  };

  const downloadCookies = () => {
    const content = cookies
      .filter(item => item.application === application && !item.valid)
      .map(item =>
        String(item.value || "")
          .trim()
          .replace(/\n/g, "")
      )
      .filter(o => o)
      .join("\n");

    const link = document.createElement("a");
    link.download = "cookies.txt";
    link.style.display = "none";
    const blob = new Blob([content]);
    link.href = URL.createObjectURL(blob);
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
  };

  const deleteCookie = async id => {
    const data = await axios.delete(`${apis.deleteCookie}/${id}`);
    if (data.code === 0) {
      deleteCookieCallback(id);
      message.success("");
    } else {
      message.error(data.message);
    }
  };

  const renderTable = () => {
    const cookieList = cookies
      .filter(item => item.application === application)
      .sort((a, b) => b.gmtCreate - a.gmtCreate);

    const columns = [
      {
        title: "",
        dataIndex: "headImgUrl",
        key: "headImgUrl",
        width: 60,
        render: (url, record) =>
          url ? <img src={url} width="50" height="50" alt="" /> : ""
      },
      {
        title: "",
        dataIndex: "time",
        key: "time",
        render: (time, record) => (
          <div
            style={{
              whiteSpace: "nowrap",
              overflow: "hidden",
              textOverflow: "ellipsis"
            }}
          >
            <p>{time}</p>
            <p>
              {!record.valid && (
                <span style={{ color: "#dd2323" }}>[]&nbsp;</span>
              )}
              {record.nickname}
            </p>
          </div>
        )
      },
      {
        title: "",
        dataIndex: "operate",
        key: "operate",
        width: 60,
        render: (text, record) => (
          <Popconfirm
            title={
              record.valid
                ? ""
                : " 5 "
            }
            onConfirm={() => deleteCookie(record.id)}
            okText=""
            cancelText=""
          >
            <Button size="small"></Button>
          </Popconfirm>
        )
      }
    ];

    return (
      <Table
        dataSource={cookieList}
        columns={columns}
        pagination={{
          pageSize: 5,
          size: "small",
          total: cookieList.length
        }}
      />
    );
  };

  return (
    <div>
      <div style={{ color: "#dd2323", marginBottom: 15 }}>
        1 cookie
        <br />
        2 cookie 
        <br />
        3 cookie 
        <br />
        4 QQ cookie 
        <br />
        5 cookie 
        <br />
        6
      </div>
      <Radio.Group
        onChange={onApplicationChange}
        value={application}
        style={{ marginBottom: "12px" }}
      >
        <Radio value={0} disabled>
          
        </Radio>
        <Radio value={1}></Radio>
        <Radio value={2}></Radio>
      </Radio.Group>
      {application === 1 && (
        <div>
          <div style={{ marginBottom: 5 }}>
            
          </div>
          <Radio.Group
            onChange={changeContributeMode}
            value={contributeMode}
            style={{ marginBottom: "12px" }}
          >
            <Radio value="sid-and-snsInfo"> SIDsnsInfo</Radio>
            <Radio value="cookie"> cookie</Radio>
          </Radio.Group>
          <ContributeForm onSubmit={handleSubmit}>
            {contributeMode === "sid-and-snsInfo" && (
              <div>
                <Form.Item>
                  {getFieldDecorator("sid", {
                    rules: [
                      { required: true, message: " cookie SID" }
                    ]
                  })(<Input placeholder=" cookie SID" />)}
                </Form.Item>
                <Form.Item>
                  {getFieldDecorator("snsInfo", {
                    rules: [
                      {
                        required: true,
                        message: " cookie snsInfo"
                      }
                    ]
                  })(
                    <Input.TextArea
                      placeholder=" cookie snsInfo"
                      autosize={{ minRows: 8, maxRows: 8 }}
                    />
                  )}
                </Form.Item>
              </div>
            )}
            {contributeMode === "cookie" && (
              <Form.Item>
                {getFieldDecorator("cookie", {
                  rules: [
                    {
                      required: true,
                      message:
                        " cookie SID  snsInfo"
                    }
                  ]
                })(
                  <Input.TextArea
                    placeholder=" cookie SID  snsInfo"
                    autosize={{ minRows: 10, maxRows: 10 }}
                  />
                )}
              </Form.Item>
            )}
            <Form.Item>
              <Button
                type="primary"
                loading={loading}
                htmlType="submit"
                className="login-form-button"
              >
                 cookie
              </Button>
            </Form.Item>
          </ContributeForm>
          {cookies.length > 0 && (
            <div>
              <Button.Group style={{ marginBottom: 15 }}>
                <Button onClick={downloadCookies}>
                  <Icon type="download" theme="outlined" />
                   cookie
                </Button>
                <Popconfirm
                  title=""
                  onConfirm={deleteCookies}
                  okText=""
                  cancelText=""
                >
                  <Button type="danger">
                    <Icon type="delete" theme="outlined" />
                     cookie
                  </Button>
                </Popconfirm>
              </Button.Group>
              <div style={{ marginBottom: 15 }}>
                 Chrome  FireFox 
              </div>
            </div>
          )}
        </div>
      )}
      {application === 2 && (
        <ContributeForm onSubmit={handleSubmit}>
          <Form.Item>
            {getFieldDecorator("cookie", {
              rules: [
                {
                  required: true,
                  message: " cookie whid  WMID"
                }
              ]
            })(
              <Input.TextArea
                placeholder=" cookie whid  WMID"
                autosize={{ minRows: 10, maxRows: 10 }}
              />
            )}
          </Form.Item>
          <Form.Item>
            <Button
              type="primary"
              loading={loading}
              htmlType="submit"
              className="login-form-button"
            >
               cookie
            </Button>
          </Form.Item>
        </ContributeForm>
      )}
      {renderTable()}
    </div>
  );
};

export default Form.create()(Contribute);



