import React, { useState, useEffect, useRef } from "react";
import { Button, message, Icon, Select, Modal } from "antd";
import Clipboard from "clipboard";
import styled from "styled-components";
import isWeixin from "is-weixin";
import { apis, axios, qs } from "../../api";

const Container = styled.div`
  position: relative;
  background: #fff;
  display: inline-block;
  z-index: 100;
`;

const Image = styled.div`
  position: absolute;
  left: 0;
  margin-top: 5px;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 4px;
  background: #fff;
  display: none;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.2);

  div {
    text-align: center;
    padding: 10px 10px 0;
  }
`;

const RmbButton = styled(({ hookRef, ...rest }) => (
  <Button ref={hookRef} {...rest} />
))`
  &:hover ${Image} {
    display: block;
  }
`;

export default ({ user }) => {
  const copyCode = useRef();
  const [ka, setKa] = useState(false);
  const [card, setCard] = useState("");
  const [application, setApplication] = useState(0);
  const [qrcode, setQrcode] = useState("");
  const [loading, setLoading] = useState(false);
  const cardName = { day: "", week: "", month: "" }[card] || "";
  const cardPrice =
    { day: "1.88", week: "8.88", month: "18.88" }[card] || "";
  const cardDays = { day: 1, week: 7, month: 30 }[card] || "";
  const weixin = isWeixin(navigator.userAgent);

  const wxPay = async () => {
    if (loading) {
      return;
    }
    setLoading(true);
    try {
      const { data, message: message2 } = await axios.post(
        apis.createPay,
        qs.stringify({
          type: weixin ? "cashier" : "native",
          user_id: user.id,
          application,
          card
        })
      );
      if (message2) {
        return message.error(message2);
      }
      if (weixin) {
        window.location.href = data;
      } else {
        setQrcode(data);
      }
    } catch (e) {
      message.error(e.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(
    () => {
      const clipboard = new Clipboard(copyCode.current.buttonNode, {
        text: () => "aRhixt096d"
      });
      clipboard.on("success", e => {
        e.clearSelection();
        message.info(
          "aRhixt096d"
        );
      });
      clipboard.on("error", () => {
        if (
          window.confirm("")
        ) {
          window.location.href =
            "https://qr.alipay.com/c1x06611gnoczzqiklpka04";
        }
      });
    },
    [copyCode]
  );

  return (
    <Container>
      <Button.Group style={{ marginBottom: 5, marginRight: 5 }}>
        <RmbButton
          type="primary"
          onClick={() => message.info("")}
        >
          <Icon type="pay-circle" />
          
          <Image>
            <table>
              <tr style={{ color: "#222" }}>
                <th></th>
                <th></th>
              </tr>
              <tr>
                <td>
                  <img
                    src={require("../../static/zfb.png")}
                    width="190"
                    alt=" "
                  />
                </td>
                <td>
                  <img
                    src={require("../../static/wx.png")}
                    width="190"
                    alt=" "
                  />
                </td>
              </tr>
            </table>
          </Image>
        </RmbButton>
        <RmbButton type="primary" hookRef={copyCode}>
          <Icon type="alipay-circle" />
          
          <Image>
            <img
              src={require("../../static/hongbao1.jpg")}
              width="290"
              alt=" "
            />
          </Image>
        </RmbButton>
      </Button.Group>
      <Button.Group>
        <RmbButton
          type="danger"
          onClick={() => {
            setKa(true);
            setCard("month");
          }}
        >
          
        </RmbButton>
        <RmbButton
          type="danger"
          onClick={() => {
            setKa(true);
            setCard("week");
          }}
        >
          
        </RmbButton>
        <RmbButton
          type="danger"
          onClick={() => {
            setKa(true);
            setCard("day");
          }}
        >
          
        </RmbButton>
      </Button.Group>
      <Modal
        title={`${cardPrice} ${cardName}(${cardDays})`}
        visible={ka}
        confirmLoading={loading}
        onOk={wxPay}
        okText=""
        onCancel={() => {
          setKa(false);
          setQrcode(null);
        }}
        cancelText=""
      >
        <div>
          <span></span>
          <Select
            defaultValue={application}
            style={{ marginBottom: "20px" }}
            onSelect={application => {
              setApplication(application);
              setQrcode(null);
            }}
          >
            <Select.Option value={0}>{cardName}-20</Select.Option>
            <Select.Option value={1}>
              {cardName}-20
            </Select.Option>
            <Select.Option value={2} disabled>
              {cardName}-
            </Select.Option>
          </Select>
        </div>
        <ul>
          <li></li>
          <li></li>
          <li>1</li>
          <li style={{ color: "#dd2323" }}>
            
          </li>
        </ul>
        {qrcode && (
          <div style={{ textAlign: "center" }}>
            <br />
            <img src={qrcode} width={200} height={200} alt="" />
            <p style={{ marginTop: 10 }}>
              
              <br />
              
            </p>
          </div>
        )}
      </Modal>
    </Container>
  );
};



