import React from "react";
import Echarts from "../../component/Echarts";

export default ({ data: { ele = [], meituan = [] }, pieData }) => {
  if (!ele.length && !meituan.length) {
    return <div style={{ textAlign: "center" }}></div>;
  }

  return (
    <div>
      <div style={{ color: "#dd2323" }}></div>
      <div></div>
      <div></div>
      <div>, </div>
      <div></div>
      <Echarts
        style={{ width: "100%", height: "300px", marginTop: "24px" }}
        key={1}
        option={{
          // color: ['#22559c', '#f27370', '#fa9856', '#ede862', '#51dacf', '#9e3668', '#ff7f5b', '#7aa5d2'],
          title: {
            text: "",
            x: "center"
          },
          tooltip: {
            trigger: "item",
            formatter: "{a} : {b} <br/>  : {d}%"
          },
          series: [
            {
              name: "",
              type: "pie",
              radius: "55%",
              center: ["50%", "60%"],
              data: pieData.meituan.map(item => ({
                value: item.proportion / 1000,
                name: item.price
              })),
              itemStyle: {
                emphasis: {
                  shadowBlur: 10,
                  shadowOffsetX: 0,
                  shadowColor: "rgba(0, 0, 0, 0.5)"
                }
              }
            }
          ]
        }}
      />
    </div>
  );
};



